.contextual-links {
  display: none;
  margin-top: -0.0625rem;
  overflow: hidden;
  text-align: left;
  white-space: nowrap;
  background-color: #fff;
  border: 0.0625rem solid #2ba9e0;
  border-radius: 0 0 0 0;
  animation: contextual-menu-right 400ms reverse;
}

[dir="rtl"] .contextual-links {
  text-align: right;
  border-radius: 0 0 0 0;
  animation: contextual-menu-left 400ms reverse;
}

.contextual-links li {
  margin-bottom: 0.0625rem;
}

.contextual-links li:last-child {
  margin-bottom: 0;
}

.contextual-links a {
  display: block;
  padding: 0.5em 0.6em;
  font-family: sans-serif;
  font-size: 1rem;
  line-height: 1.1;
  text-decoration: none;
  color: #000;
  background-color: #fff;
}

.touchevents .contextual-links a {
